.app {
  min-height: 100vh;
  background-color: #fff;
}

.app-main {
  padding-bottom: 32px;
}

/* Animasi memuat (opsional) */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.app-main > * {
  animation: fadeIn 0.3s ease-in-out;
}

/* Penataan scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.note-modal {
    position: fixed;
    inset: 0;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999;
}

.note-modal-content {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 90%;
    max-height: 90%;
    pointer-events: none;
}

.note-modal-image {
    max-width: 80vw;
    max-height: 80vh;
    object-fit: contain;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
    transition: transform 0.2s ease-in-out;
    pointer-events: auto;
}

.note-modal-image:hover {
    transform: scale(1.01);
}

